* {
  box-sizing: border-box;
  -webkit-user-select: none;
  user-select: none;
  margin: 0;
  padding: 0;
  font-family: Kalam, cursive;
}

html, body {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

h1, h2, p, span {
  text-shadow: 1px 1px #000;
}

header {
  color: #ccc;
  width: 100%;
  padding: .5rem 1rem;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

header > .logo {
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold;
}

header > .logo > span {
  margin-left: 10%;
}

header div {
  flex-grow: 1;
}

header > .nav-holder {
  z-index: 100;
  position: relative;
}

header > .nav-holder > button {
  float: right;
  color: #ccc;
  cursor: pointer;
  background-color: #0000;
  border: none;
  margin-right: 10%;
  font-size: 2rem;
}

header > .nav-holder > .menu {
  width: 400px;
  height: min-content;
  display: flex;
  position: absolute;
  top: 3rem;
  right: -1rem;
  transform: translate(400px);
}

header > .nav-holder > .menu > nav {
  display: flex;
  position: relative;
}

header > .nav-holder > .menu > nav > div {
  background-color: #fff;
  width: 1px;
  height: 110%;
  position: absolute;
  top: -10%;
  right: 0;
  transform: scaleY(0);
}

header > .nav-holder > .menu > nav > .spiner {
  z-index: 1;
  transform-origin: top;
  clip-path: polygon(50% 50%, 100% 50%, 100% 50%, 50% 50%, 50% 50%, 50% 50%, 50% 50%, 50% 50%, 50% 50%, 50% 50%);
  background-color: #fff;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  position: absolute;
  top: -25px;
  right: -6px;
}

header > .nav-holder > .menu > nav > ul > li {
  text-align: right;
  cursor: pointer;
  opacity: 0;
  padding-right: 2rem;
  font-size: 3rem;
  font-weight: bold;
  list-style: none;
  position: relative;
}

header > .nav-holder > .menu > nav > ul > li > span > a {
  color: #000;
  text-shadow: -1px -1px #aaa, 1px -1px #aaa, -1px 1px #aaa, 1px 1px #aaa;
  text-decoration-style: dotted;
  text-decoration-color: #793ea5;
  text-decoration-thickness: 1px;
  transition: all .5s;
}

header > .nav-holder > .menu > nav > ul > li:hover > span > a {
  color: #aaa;
}

header > .nav-holder > .menu > nav > ul > li:after {
  content: "";
  z-index: 1;
  background-color: #fff;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  transition: all .5s;
  position: absolute;
  top: 50%;
  right: -8px;
  transform: translate(-50%, -50%);
}

header > .nav-holder > .menu > nav > ul > li:hover:after {
  background-color: #fff;
}

header > .nav-holder > .menu > .nav-animation {
  flex-direction: column;
  flex-grow: 3;
  padding-left: 1rem;
  display: flex;
}

header > .nav-holder > .menu > .nav-animation div {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-around;
  display: flex;
}

header > .nav-holder > .menu > .nav-animation div hr {
  width: 100%;
  transform: translate(100%);
}

section {
  z-index: 1;
  color: #fff;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 5%;
  display: flex;
  position: absolute;
  left: 0;
}

.transform {
  padding-top: 5%;
  width: 30%;
  padding-inline: 1rem;
  position: relative;
  transform: translate(-200%);
}

.uvLampDiv {
  z-index: 10;
  background: #111;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0%;
  left: 0%;
}

.uvLampDiv > .lamp {
  background-color: #fff;
  border-radius: 3px;
  width: 5px;
  height: 100%;
  transform: scale(0);
  box-shadow: 0 0 20px #0481b3, 0 0 40px #0481b3, 0 0 80px #0481b3, 0 0 160px #0481b3;
}

.fire {
  width: 0;
  height: 0;
}

section .fireH2 {
  filter: url("#fire");
  color: #ff8c3b;
  text-shadow: 0 0 10px #ff8c3b, 0 0 40px #ff8c3b, 0 0 80px #ff8c3b;
  -webkit-box-reflect: below -10px linear-gradient(transparent, #0003);
  font-size: 2rem;
}

section div .jumping-letter span {
  color: #fec400;
  animation: 3s infinite jumpingLetter;
  display: inline-block;
  position: relative;
}

@keyframes jumpingLetter {
  0% {
    transform: translateY(0%)rotate(0);
  }

  40% {
    transform: translateY(0%)rotate(0);
  }

  50% {
    transform: translateY(-50%)rotate(180deg);
  }

  60% {
    transform: translateY(0%)rotate(360deg);
  }

  100% {
    transform: translateY(0%)rotate(360deg);
  }
}

.jumping-letter > span:first-child {
  animation-delay: .5s;
}

.jumping-letter > span:nth-child(2) {
  animation-delay: .2s;
}

.jumping-letter > span:nth-child(3) {
  animation-delay: .7s;
}

.jumping-letter > span:nth-child(4) {
  animation-delay: .9s;
}

.jumping-letter > span:nth-child(5) {
  animation-delay: .3s;
}

.jumping-letter > span:nth-child(6) {
  animation-delay: .5s;
}

.jumping-letter > span:nth-child(7) {
  animation-delay: 1s;
}

.jumping-letter > span:nth-child(8) {
  animation-delay: .6s;
}

.jumping-letter > span:nth-child(9) {
  animation-delay: 1.1s;
}

.jumping-letter > span:nth-child(10) {
  animation-delay: 1.3s;
}

.jumping-letter > span:nth-child(11) {
  animation-delay: .2s;
}

.jumping-letter > span:nth-child(12) {
  animation-delay: .9s;
}

h1, h2 {
  color: #793ea5;
  text-align: center;
  margin-bottom: 1rem;
}

p {
  text-indent: 2rem;
  text-align: justify;
  font-size: 1rem;
}

p span {
  color: #000;
  background-color: #ff0;
  padding-inline: .3rem;
  font-weight: 900;
}

.socialLinks {
  writing-mode: vertical-lr;
  justify-content: flex-end;
  align-items: flex-end;
  height: 100%;
}

.socialLinks span {
  color: #ccc;
  cursor: pointer;
  font-size: 1.7rem;
  transition: all .5s;
}

.socialLinks span:first-child {
